import { inject, Inject, Injectable, Optional, PLATFORM_ID } from '@angular/core';
import { isPlatformServer } from '@angular/common';
@Injectable()
export class ResponseService {
	platformId = inject(PLATFORM_ID);
	constructor(
		@Optional() @Inject('REQUEST') private req: any,
		@Optional() @Inject('RESPONSE') private res: any
	) {
		// Only executes server-side
	}

	setStatusCode(code) {
		if (isPlatformServer(this.platformId) && this.res?.statusCode) {
			this.res.statusCode = code;
		}
	}
}
